// import Alpl from 'Molecules/animation-line-per-line';
// import anime from 'animejs/lib/anime.es.js';
import Anime3d from 'Molecules/animation-3d';
import smart_fonts from 'Libs/smart-fonts.js';

//$ = window.jquery

import velocity from 'velocity-animate'
window.jquery.prototype.velocity = velocity

import is from "is_js"


export default {

	components: {

		// Alpl,
		// anime,
		Anime3d,
		// textcotact,
	
	},

	props: {
		'section' : Object,
		'post' : Object
	},

	mounted(){

		let $ = this.$

		this.wp = window.wp

		console.log( this.post );

	if(  is.not.mobile() && is.not.tablet()  )
	{

		$('.image-client').on('enter-screen', (event) => {
			//console.log('enter-screen', event.target);
			/*$(event.target).velocity({
				'opacity': 1,
				},
				800);*/

			//console.log(event.target);

			/*$({blurRadius: 10, translateY: 50}).velocity({blurRadius: 0, translateY: 0}, {
				duration: 800,
				easing: 'easeInOutQuad', // or "linear"
				// use jQuery UI or Easing plugin for more options
				step: function() {
					console.log('step velocity');
					$(event.target).css({
						"-webkit-filter": "blur("+this.blurRadius+"px)",
						"filter": "blur("+this.blurRadius+"px)",
						"transform": "translateY("+ this.translateY +"px)"
					});
				},

			});*/

			$(event.target).velocity({
				opacity: 1,
			}, {
				easing: "easeInOutQuad",
				duration: 800,
				progress: function(elements, complete) {

					let translateY = (50 - 50 * complete)
					let blurRadius = (10 - 10 * complete)

					$(elements).css({
						"-webkit-filter": "blur("+blurRadius+"px)",
						"filter": "blur("+blurRadius+"px)",
						"transform": "translateY("+ translateY +"px)"
					});
				}
			});
		})

		//on leave screen (also tiggered on init)
		$('.image-client').on('leave-screen', (event) => {

			/*$(event.target).stop().animate({
				'opacity': 0,
				},
				800, function() {

			});*/
			//console.log(event.target);
			/*
			$({blurRadius: 0, translateY: 0}).stop().animate({blurRadius: 10, translateY: 50}, {
				duration: 800,
				easing: 'easeInOutQuad', // or "linear"
				// use jQuery UI or Easing plugin for more options
				step: function() {
					//console.log(this.blurRadius);
					$(event.target).css({
						"-webkit-filter": "blur("+this.blurRadius+"px)",
						"filter": "blur("+this.blurRadius+"px)",
						"transform": "translateY("+ this.translateY +"px)"
					});
				},

			});*/

			$(event.target).velocity({
				opacity: 0,
			}, {
				easing: "easeInOutQuad",
				duration: 800,
				progress: function(elements, complete) {

					let translateY = (50 * complete)
					let blurRadius = (10 * complete)

					$(elements).css({
						"-webkit-filter": "blur("+blurRadius+"px)",
						"filter": "blur("+blurRadius+"px)",
						"transform": "translateY("+ translateY +"px)"
					});
				}
			});
		})


		}
		else{

			$('.image-client').css('opacity', '1');
			$('.image-client').css('-webkit-filter', 'blur(0px)');

		}
		this.$emit('template_mounted', this)

		// $('.global-project').css({
		// 	// "opacity":"0",
		// 	transform: 'translateY(100px)',
		// 	opacity: 0,
		// });
		


		// $('.global-project').on('leave-screen', (event) => {

		// 	$(event.target).css({
		// 		// "opacity":"0",
		// 		// transform: 'translateY(100px)',
		// 		opacity: 0,
		// 	});

