		// 	});*/
		// })

		// $( ".global-project" ).each( function( index , el) {

		// 	$(el).on('enter-screen', (event) => {
		// 		anime({
		// 			targets: event.target,
		// 			// translateY: '0px',
		// 			opacity: 1,
		// 			easing: 'easeInOutSine',
		// 			duration: 500,
		// 			delay: index * 200
		// 		});
		// 	})
		// });
		smart_fonts({
		'.smallTile' :54,
		'.text-homepage' : 50,
		'.name-client': 22,
		})

	

	}




}



</script>

